import React, { useState } from 'react';
import words from '../data/words.json'; // JSON
import { Button } from '@mui/material';

const WordTyping: React.FC = () => {
  const [currentIndex, setCurrentIndex] = useState(0); // 
  const [userInput, setUserInput] = useState(''); // 
  const [isCorrect, setIsCorrect] = useState(false); // 

  const currentWord = words[currentIndex];

  // 
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setUserInput(value);

    // 
    if (value === currentWord.english) {
      setIsCorrect(true);
    } else {
      setIsCorrect(false);
    }
  };

  // 
  const handleNextWord = () => {
    setUserInput(''); // 
    setIsCorrect(false); // 
    setCurrentIndex((prevIndex) => (prevIndex + 1) % words.length); // 
  };

   // 
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      handleNextWord(); // 
    }
  };

  return (
    <div style={{ textAlign: 'center', color: 'white', marginTop: '20px' }}>
  <h2 style={{ fontSize: '48px', margin: '20px 0' }}>{currentWord.japanese}</h2>
  {/*  */}
  <p style={{ fontSize: '40px', margin: '10px 0', color: 'lightgray' }}>
    {currentWord.english}
  </p>
  {/*  */}
  <div
  style={{
    display: 'flex',
    position: 'relative', // 
    margin: '20px auto', // 20px
    width: '500px', // 
  }}
>
  {/*  */}
  <input
    type="text"
    value={userInput}
    onChange={handleInputChange}
    onKeyDown={handleKeyDown}
    style={{
      padding: '20px',
      fontSize: '40px',
      width: '100%', // 
      textAlign: 'center',
      color: isCorrect ? 'lightgreen' : 'white',
      backgroundColor: '#1e1e2f',
      border: isCorrect ? '2px solid lightgreen' : '2px solid gray',
      borderRadius: '10px',
    }}
  />
  {/*  */}
  {isCorrect && (
    <img
      src="/images/enterkey.png"
      alt=""
      onClick={handleNextWord}
      style={{
        position: 'absolute', // 
        top: '50%', // 
        left: '510px', // 200px + 
        transform: 'translateY(-50%)', // 
        width: '80px',
        height: '80px',
        cursor: 'pointer',
      }}
    />
  )}
</div>
</div>
  );
};

export default WordTyping;
